import React from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Tooltip,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import {
  Home as HomeIcon,
  Work as WorkIcon,
  School as SchoolIcon,
  Description as DescriptionIcon,
  Apps as AppsIcon,
  ContactSupport as ContactSupportIcon,
} from "@mui/icons-material";
import Logo from "./logo";
import TranslateIcon from "@mui/icons-material/Translate";
function Header({
  lang,
  toggleLang,
  theme,
  toggleTheme,
}: {
  lang: string;
  toggleLang: () => void;
  theme: string;
  toggleTheme: () => void;
}) {
  const [drawerOpen, setDrawerOpen] = React.useState(false);
  console.log(lang);
  const toggleDrawer =
    (open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
      if (
        event.type === "keydown" &&
        ((event as React.KeyboardEvent).key === "Tab" ||
          (event as React.KeyboardEvent).key === "Shift")
      ) {
        return;
      }
      setDrawerOpen(open);
    };

  const iconTextColor = "text-slate-600";
  const menuItems = [
    {
      text: lang === "en" ? "Home" : "صفحه‌ی اصلی",
      icon: <HomeIcon className={iconTextColor} />,
    },
    {
      text: lang === "en" ? "Experience" : "تجارب کاری",
      icon: <WorkIcon className={iconTextColor} />,
    },
    {
      text: lang === "en" ? "Education" : "تحصیلات",
      icon: <SchoolIcon className={iconTextColor} />,
    },
    {
      text: lang === "en" ? "Resume" : "رزومه",
      icon: <DescriptionIcon className={iconTextColor} />,
    },
    {
      text: lang === "en" ? "Portfolio" : "پورتوفیلو",
      icon: <AppsIcon className={iconTextColor} />,
    },
    {
      text: lang === "en" ? "Blog" : "بلاگ",
      icon: <ContactSupportIcon className={iconTextColor} />,
    },
  ];

  return (
    <AppBar
      className="header-default black-logo-version shadow-lg bg-white"
      position="sticky"
      style={{ backgroundColor: "white" }}
    >
      <Toolbar className="flex justify-between items-center lg:mx-20">
        <Logo />
        <div className="flex items-center">
          <nav className="mainmenu-nav navbar-example2 hidden lg:block">
            <ul className="primary-menu flex flex-col space-y-2 lg:flex-row lg:space-x-2 lg:space-y-0">
              {menuItems.map((item, index) => (
                <li className="nav-item" key={index}>
                  <Tooltip title={item.text}>
                    <IconButton color="inherit">{item.icon}</IconButton>
                  </Tooltip>
                </li>
              ))}
              <li className="nav-item" key="change language">
                <Tooltip
                  title={lang === "en" ? "fa" : "en"}
                  onClick={toggleLang}
                >
                  <IconButton color="inherit">
                    <TranslateIcon className={iconTextColor} />
                  </IconButton>
                </Tooltip>
              </li>
            </ul>
          </nav>
        </div>
        <div className="block lg:hidden">
          <Tooltip title="menu">
            <IconButton
              className="hamberger-menu"
              color="inherit"
              aria-label="menu"
              onClick={toggleDrawer(true)}
            >
              <MenuIcon className="text-slate-600" />
            </IconButton>
          </Tooltip>
          <Drawer
            anchor={lang === "fa" ? "left" : "right"}
            open={drawerOpen}
            onClose={toggleDrawer(false)}
            className="drawer-menu rounded-lg"
          >
            <div className="p-4 pt-20">
              <List>
                {menuItems.map((item, index) => (
                  <ListItem button key={index}>
                    <ListItemIcon>{item.icon}</ListItemIcon>
                    <ListItemText primary={item.text} />
                  </ListItem>
                ))}
                <ListItem button key={"lang"} onClick={toggleLang}>
                  <ListItemIcon>
                    <TranslateIcon />
                  </ListItemIcon>
                  <ListItemText primary={lang === "en" ? "فارسی" : "English"} />
                </ListItem>
              </List>
            </div>
          </Drawer>
        </div>
      </Toolbar>
    </AppBar>
  );
}

export default Header;
